import { ApiService } from './../../../services/api.service';
import { Component, OnInit } from '@angular/core';
import { Router } from '@angular/router';
import { LoadingController, AlertController } from '@ionic/angular';

@Component({
    selector: 'app-minhas-atividades',
    templateUrl: './minhas-atividades.page.html',
    styleUrls: ['./minhas-atividades.page.scss'],
})
export class MinhasAtividadesPage implements OnInit {
    textos = [{
        titulo: 'Soneto VII',
        autor: 'Gregório de Matos'
    }, {
        titulo: 'Soneto',
        autor: 'Cláudio Manoel da Costa'
    }, {
        titulo: 'Amor',
        autor: 'Álvares de Azevedo'
    }, {
        titulo: 'Deixe que o Olhar',
        autor: 'Olavo Bilac'
    }, {
        titulo: 'O Quinze',
        autor: 'Rachel de Queiroz'
    }, {
        titulo: 'Oceano',
        autor: 'Djavan'
    }, {
        titulo: 'Soneto',
        autor: 'Alphonsus de Guimaraens'
    }]
    atividades = []
    constructor(
        public router: Router,
        private api: ApiService,
        public loadingCtrl: LoadingController,
        public alertCtrl: AlertController
    ) { }

    ngOnInit() {
    }
    ionViewDidEnter() {
        this.buscarAtividades()
    }

    adicionarAtividade() {
        console.log("Nova Atividade");

        this.router.navigate(['/atividades/nova-atividade']);
    }

    async buscarAtividades() {
        let loading = await this.loadingCtrl.create({ message: "Buscando atividades..." })
        loading.present()
        let body = `
            query {
                buscarAtividades{
                    id
                    titulo
                    autor
                }
            }
        `
        let retorno = await this.api.get(body)
        loading.dismiss()
        console.log('retorno: ', retorno);

        if (!retorno.errors && retorno.data.buscarAtividades) {
            this.atividades = retorno.data.buscarAtividades
        }else {
            let alerta = await this.alertCtrl.create({ header: "Erro", message: "Ocorreu um erro, tente novamente." })
            alerta.present()
        }
    }

    async abrirAtividade(atividade){
        this.router.navigate(['atividades/responder-atividade/'+atividade.id])
        // let loading = await this.loadingCtrl.create({ message: "Buscando atividades..." })
        // loading.present()
        // let body = `
        //     query {
        //         retornarAtividade(id_atividade: ${atividade.id}){
        //             id
        //             titulo
        //             autor
        //             texto
        //             imagens{
        //                 imagem{
        //                     link
        //                 }
        //             }
        //         }
        //     }
        // `
        // let retorno = await this.api.get(body)
        // loading.dismiss()

    }
}
